* {
    margin: 0;
    border: 0;
    padding: 0;
    font-family: 'Inter', sans-serif;
    box-sizing: border-box;
}

body {
    background: #1C1C1C;
}


.container {
    width: 500px;
    margin: 50px auto;
    background: #23252C;
    box-shadow: rgba(50, 50, 93, 0.25) 20px 30px 60px -12px, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}

.header {
    text-align: center;
    color: #fff;
}

.header img {


}

.header h1{
    margin-top: 0.5em;
    font-weight: 700;
    font-size: 2em;
    line-height: 1.4em;
}

.header p {
    color: #F3BF99;
    font-weight: 400;
    font-size: 1em;
    line-height: 1.2em;
    padding: 0.5em;
}

.header a {
    color: #fff;
    text-decoration: none;
}

.navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0 30px;
    margin: 20px 35px;
}

.navbar a {
    color: black;
    font-size: larger;
    font-weight: 500;
    text-decoration: none;
    background-color: #fff;
    width: 175px;
    padding: 15px;
    text-align: center;
    border-radius: 10px;
}

.navbar a:first-child::before {
    content: '';
    background-image: url('./components/image/github.png');
    background-size: 20px;
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-right: 10px;
}

.navbar a:nth-child(2)::before {
    content: '';
    background-image: url('./components/image/facebook.png');
    background-size: 20px;
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-right: 10px;
}

.navbar a:nth-child(1) {
    color: white;
    background-color: #5093E2;
}

section {
    color: white;
    margin: 40px 30px;
}

h3 {
    font-weight: 700;
    line-height: 24px;
    margin: 10px 0;
}

.footer {
    background-color: #161619;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 30px 50px;
}

.footer img {
    width: 40px;

}
